import React from "react";
import styled from "styled-components";

// Components
import Blackbar from "./Blackbar";
import Information from "./Information";

// Assets
import { Media } from "../../../Assets/Variables/";
import LanguageContext from "../../../Context/Language";
/*

    etta er undir Reusable v a vi tlum a nota etta lka fyrir golfi,  sta ess a copy/paste'a etta 
    undir HorseRental og SinglePageCottages.


    BlackbarInformation inniheldur upplsingar fyrir Svarta og ljsbrna kassann  Sumarbstaar sunum.
    Sendir niur  Information(Ljsbrni kassinn) og Blackbar.

*/

const BlackbarInformation = Data => {
  const {English} = React.useContext(LanguageContext);
  
  return (
  <Container>
    <Blackbar {...Data} English={English} />
    <Information {...Data} English={English}  />
  </Container>
)};

export default BlackbarInformation;

const Container = styled.div`
  display: flex;
  width: 80%;
  margin: 0px auto;
  height: 1px;
  align-items: center;

  ${Media.large`width: 100%;`};
  ${Media.desktop`
  height: 100%;
  flex-direction: column-reverse;
  `};
`;
